export default {
	data() {
		return {
			limit: 15,
			page: 1,
			record:[],
			moreData:'暂无数据',
			totalCount:0
		};
	},
	onReachBottom() {
	   let totalPage =  Math.ceil(this.totalCount/this.limit)
		if(this.page < totalPage){
			this.page ++;
			this.getInvoice();
		}
	},
	methods:{
		getInvoice(){
			let data ={
				page: this.page,
				limit: this.limit
			}
		},
		timestampTime(time){
			return this.$uitls.timestampToTime(time);
		},
		showInvoice(item){
			if(!item.filepath){
				uni.showToast({
					icon:'none',
					title:'此订单暂无发票'
				})
				return
			}
		 let imgShowUrl = 	this.$store.state.invoiceBaseUrl + item.filepath;
		 uni.downloadFile({
		   url: imgShowUrl,
		   success: function (res) {
		     var filePath = res.tempFilePath;
		     uni.openDocument({
		       filePath: filePath,
		       success: function (res) {
		         console.log('打开文档成功');
		       }
		     });
		   }
		 });
		 
		}
	},
	onLoad() {
		this.getInvoice();
	}
}
